import axios from "axios";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      url: "/photo/save",
      method: "post",
      data: JSON.stringify({}),
      dialog: false,
      error: false,
      selectedFile: null,
      text: "",
      game: "",
      game_russian: "",
      bgg: "",
      community: "",
      playingtime: "",
      recommendedage: "",
      designer: "",
      author: "",
      publisher: "",
      description: "",
      photoid: null,
      image: null,
    };
  },

  computed: {
    error() {
      return this.$store.getters.error;
    },
    /*dataUrl() {
      return "data:image/jpeg;base64," + btoa(this.image);
    },*/
    dataUrl() {
      return (
        "data:image/jpeg;base64," +
        btoa(
          new Uint8Array(this.image).reduce(
            (data, byte) => data + String.fromCharCode(byte),
            ""
          )
        )
      );
    },
  },

  methods: {
    ...mapActions(["loadAllBoardgames"]),

    onFileChanged(event) {
      this.selectedFile = event.target.files[0];
    },
    onUpload() {
      const formData = new FormData();
      formData.append("image", this.selectedFile, this.selectedFile.name);
      axios
        .post("/photo/save", formData, {
          headers: {
            Authorization: `Bearer ${this.$store.getters.token}`,
            "Content-Type": "multipart/form-data",
          },
        })
        .then((data) => {
          if (data.data > -1) {
            this.photoid = data.data;
            console.log(
              "file " + this.selectedFile.name + " uploaded successfully"
            );
            this.method = "get";
            this.url = "/photo/info/" + this.selectedFile.name;
            this.data = JSON.stringify({});
            this.getDataFromApi().then((data) => {
              if (data.data == "") {
                this.text = "Error by download photo";
                this.error = true;
                return;
              } else {
                this.image = data.data.image;
              }
            });
          } else {
            this.text = "There was a problem uploading the file to the server";
            this.error = true;
          }
        })
        .catch(function () {
          console.log("FAILURE by uploading the file");
        });
    },

    onCreate() {
      if (this.game == "") {
        this.text = "Game name wasn't filled";
        this.error = true;
      } else {
        if (this.photoid == null) {
          this.text = "Image schould be upload";
          this.error = true;
        } else {
          this.method = "post";
          this.url = "/boardgame/new";
          this.data = JSON.stringify({
            nameEnglish: this.game,
            nameRussian: this.game_russian,
            author: this.author,
            designer: this.designer,
            publishingHouse: this.publisher,
            issuiYear: null,
            genre: null,
            numberPlayers: this.community,
            recommendedNumberPlayers: null,
            age: this.recommendedage,
            partyTime: this.playingtime,
            bggRating: this.bgg,
            description: this.description,
            photoid: this.photoid,
          });
          this.getDataFromApi().then((data) => {
            this.error = false;
            this.dialog = false;
            this.game = null;
            this.game_russian = null;
            this.author = null;
            this.designer = null;
            this.community = null;
            this.recommendedage = null;
            this.playingtime = null;
            this.bgg = null;
            this.description = null;
            this.photoid = null;
            this.selectedFile = null;
            this.image = null;
            this.publisher = null;
            console.log(data.data);
            this.loadAllBoardgames();
          });
        }
      }
    },

    getDataFromApi() {
      var config;

      config = {
        method: this.method,
        url: this.url,
        headers: {
          Authorization: `Bearer ${this.$store.getters.token}`,
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        data: this.data,
      };
      return axios(config);
    },

    close() {
      this.error = false;
      this.dialog = false;
    },

    onDismissed() {
      console.log("Dismissed Alert");
      this.error = false;
    },
  },
};
